.welcomePage {
  position: relative;
  width: 100%;
  padding: 60px 47px;
  .head {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: -1;
    height: 355px;
    overflow: hidden;
    background-color: #242a2e;
    .circle {
      position: absolute;
      border-radius: 50%;
    }
    .circle1 {
      top: 126px;
      right: 142px;
      width: 92px;
      height: 92px;
      background-color: #90a9b5;
    }
    .circle2 {
      top: 73px;
      right: 89px;
      width: 198px;
      height: 198px;
      background-color: #6a9bb4;
    }
    .circle3 {
      top: -20px;
      right: -4px;
      width: 384px;
      height: 384px;
      background-color: #3e83b5;
    }
    .circle4 {
      top: -93px;
      right: -77px;
      width: 530px;
      height: 530px;
      background-color: #1e699f;
    }
    .circle5 {
      top: -405px;
      right: -389px;
      width: 1154px;
      height: 1154px;
      background-color: #24343d;
    }
  }
  .eventPage {
    width: 394px;
    height: 66px;
    background: rgba(27, 28, 30, 0.7);
    border-radius: 33px;
    float: right;
    display: flex;
    .slices {
      width: 88px;
      height: 56px;
      margin: 9px 0 0 7px;
    }
    .content {
      width: 210px;
      margin: 5px 14px 0 7px;
      font-size: 12px;
      font-family: PingFangTC-Semibold, PingFangTC;
      font-weight: 600;
      color: #f3f2f0;
      line-height: 15px;
      .content_title {
        width: 77px;
        height: 22px;
        font-size: 16px;
        color: #f1f0ee;
        line-height: 22px;
      }
    }
  }
  .divide {
    width: 100%;
    height: 1px;
    margin: 48px 0;
  }
  .first-level-title {
    margin-bottom: 48px;
    font-size: 32px;
  }
  .first-level-title-base {
    font-size: 32px;
  }
  .second-level-title {
    margin-top: 13px;
    margin-bottom: 25px;
    font-size: 13px;
    line-height: 17px;
  }
  .ide-introduction {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin-top: 23px;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 17px;
    white-space: nowrap;
    img {
      width: 17px;
      margin-right: 6px;
      margin-left: 6px;
    }
  }
  .ide-resource {
    position: relative;
    display: flex;
    margin-top: 30px;
    flex-wrap: wrap;
    overflow: hidden;
    background-color: rgba(32, 33, 35, 0.7);
    border-radius: 8px;
    &::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      backdrop-filter: saturate(180%) blur(20px);
      content: '';
    }

    .resource-card {
      position: relative;
      flex: 1;
      right: 1px;
      padding: 51px 20px 53px 20px;
      text-align: center;
      cursor: pointer;
      &::before {
        position: absolute;
        top: 51px;
        bottom: 53px;
        left: 0;
        width: 1px;
        background-color: #273c46;
        content: '';
      }
      &:first-child:before {
        display: none;
      }
      img {
        width: 67px;
        cursor: pointer;
      }
      .resource-card-title {
        margin-top: 16px;
        font-size: 17px;
        line-height: 22px;
      }
      .resource-card-subtitle {
        width: 100%;
        height: 17px;
        margin-top: 10px;
        font-size: 13px;
        line-height: 17px;
      }
    }
  }
  .template-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -55px;
    .template {
      margin-left: 40px;
      margin-top: 21px;
      width: 317px;
      height: 138px;
      background: #292929;
      border-radius: 2px;
      cursor: pointer;
      .header {
        display: flex;
        align-items: center;
        padding: 18px 25px 9px 12px;

        img {
          width: 21px;
          height: 21px;
          margin-right: 12px;
          object-fit: contain;
        }
        .title {
          flex-grow: 1;
          font-weight: 600;
        }
      }
      .describe {
        padding-right: 30px;
        padding-left: 42px;
        color: #928e8a;
      }
      &:hover {
        background-color: #393a3b;
      }
    }
  }
}
