.container {
  display: flex;
  justify-content: space-between;
  margin: 15px 30px;
  border-bottom: 1px solid #393a3b;
}

.left {
  flex: 1;
  min-width: 200px;
  max-width: 80%;
}

.right {
  display: flex;
  flex: none;
  flex-direction: column;
  padding: 10px 0;
}

.header {
  height: 35px;
  font-size: 13px;
  line-height: 35px;
}

.title {
  display: inline-block;
  width: 70%;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
  text-overflow: ellipsis;
}

.count {
  display: inline-block;
  width: 30%;
  overflow: hidden;

  white-space: nowrap;
  text-align: right;
  text-overflow: ellipsis;
}

.jsonTree {
  margin: 13px 20px 30px 0;

  ul {
    background-color: transparent !important;
  }
}

.hidden {
  display: none !important;
}

.flexJustifyContentEnd {
  justify-content: flex-end;
}
